import { Router } from 'express'
import UsersController from './Users'
import FileController from './File'
import ConfigController from './Config'
import CommonController from './Common'
import XueshengController from './Xuesheng'
import QiyeController from './Qiye'
import ZhiweileibieController from './Zhiweileibie'
import ZhaopinxinxiController from './Zhaopinxinxi'
import YingpinxinxiController from './Yingpinxinxi'
import XuanjianghuiController from './Xuanjianghui'
import XuanjianghuibaomingController from './Xuanjianghuibaoming'
import QiuzhijianliController from './Qiuzhijianli'
import JiuyexinxiController from './Jiuyexinxi'
import StoreupController from './Storeup'
import NewsController from './News'
import AboutusController from './Aboutus'

export default ({ config, db }) => {
	let api = Router()

	api.use('/users', UsersController({ config, db }))
	api.use('/file', FileController({ config, db }))
	api.use('/config', ConfigController({ config, db }))
	api.use('/', CommonController({ config, db }))
	api.use('/xuesheng', XueshengController({ config, db }))
	api.use('/qiye', QiyeController({ config, db }))
	api.use('/zhiweileibie', ZhiweileibieController({ config, db }))
	api.use('/zhaopinxinxi', ZhaopinxinxiController({ config, db }))
	api.use('/yingpinxinxi', YingpinxinxiController({ config, db }))
	api.use('/xuanjianghui', XuanjianghuiController({ config, db }))
	api.use('/xuanjianghuibaoming', XuanjianghuibaomingController({ config, db }))
	api.use('/qiuzhijianli', QiuzhijianliController({ config, db }))
	api.use('/jiuyexinxi', JiuyexinxiController({ config, db }))
	api.use('/storeup', StoreupController({ config, db }))
	api.use('/news', NewsController({ config, db }))
	api.use('/aboutus', AboutusController({ config, db }))

	return api
}
